import React, { useEffect, useState, useContext } from "react";
import {
  AppBar,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import TabList from "../Common/Tabs/TabList";
import Print from "../Print/Print";
import Control from "../Control/Control";
import Menu from "../Menu/Menu";
import Tune from "../Tune/Tune";
import SliceModal from "../Common/SliceModal";
import { useMountEffect } from "../../Utility/useMountEffect";
import { getStorage, fillStorage } from "../../Utility/helpers";
import TempContext from "../../TempContext";

import "./Home.css";
const useStyles = makeStyles((theme) => ({
  appBar: {
    top: "auto",
    bottom: 0,
    textAlign: "center",
    padding: 0
  },
  okButton: {
    backgroundColor: "#0072CE",
    color: "white",
    marginRight: 10,
    marginBottom: 10
  }
}));

async function checkOnline() {
  const response = await fetch(
    `http://${window.location.hostname}:2127/api/v1/check/internet/connection`,
    {
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      }
    }
  );
  return response.json(); // parses JSON response into native JavaScript objects
}

async function checkUpdates() {
  // Default options are marked with *
  const response = await fetch(
    `http://${window.location.hostname}:2127/api/v1/update/software/check`,
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      }
    }
  );
  return response.json(); // parses JSON response into native JavaScript objects
}

const tabList = ["Print", "Prepare", "Control", "Tune", "Menu"];
const Home = ({ lastMessage, mode, setActiveMode }) => {
  const context = useContext(TempContext);
  const classes = useStyles();
  const [activeTab, setActiveTab] = useState("Print");
  const [disabledNav, setDisabledNav] = useState(false);
  const [newVersionTitle, setNewVersionTitle] = useState(false);
  const [releaseNotes, setReleaseNotes] = useState(false);
  const [fastTrackUpdate, setFastTrackUpdate] = useState(false);

  useMountEffect(() => {
    fillStorage().then(function() {
       if(getStorage("allowAutoUpdate")) {
         checkOnline().then((onlineData) => {
           //after api call
           if (onlineData.online) {
             checkUpdates().then((updateData) => {
               console.log(updateData);
               //after api call
               if (updateData.update) {
                 setNewVersionTitle(updateData.message);
                 setReleaseNotes(updateData.releaseNotes);
               }
             });
           }
         });
       }
     });
  });

  React.useEffect(() => {
    if (
      typeof lastMessage !== "undefined" &&
      lastMessage &&
      typeof lastMessage.data !== "undefined" &&
      lastMessage.data.length > 1
    ) {
      let json = JSON.parse(lastMessage.data.substring(1));

      if (json[0].hasOwnProperty("current") && json[0].current.progress) {
        if(
          json[0].current.progress &&
          json[0].current.job &&
          json[0].current.job.file.name &&
          json[0].current.progress.completion < 100 &&
          json[0].current.state &&
          json[0].current.state.text !== "Operational"
        ) {
          setActiveTab('Print');
          setDisabledNav(true);
        } else {
          setDisabledNav(false);
        }
      }

      if (json[0].hasOwnProperty("current") && json[0].current.messages.length) {
        let messages = json[0].current.messages;
        for (let idx in messages) {
          if(!messages[idx].includes("echo:") && messages[idx].includes("Probe Offset")) {
            let values = messages[idx].split(" ");
            let offset = values[values.length - 1];
            let offsetIgnoreZ = offset.substring(1);
            console.log(offsetIgnoreZ);
            context.setZOffset(offsetIgnoreZ);
          }
        }
      }

    }
  }, [lastMessage]);

  const handleTabClick = (event, val) => {
    setActiveTab(tabList[val]);
  };
  const Components = {
    Print: <Print lastMessage={lastMessage} setDisabledNav={setDisabledNav} />,
    Control: <Control lastMessage={lastMessage} />,
    Menu: <Menu lastMessage={lastMessage} fastTrackUpdate={fastTrackUpdate} setFastTrackUpdate={setFastTrackUpdate} />,
    Tune: <Tune lastMessage={lastMessage} />,
    Prepare: <SliceModal file={null} shown={true} onClose={() => setActiveTab('Print')} sliceOnly={true} />
  };
  return (
    <div className="home-container">
      <AppBar
        position="fixed"
        color="primary"
        className={classes.appBar}
        disabled={true}
      >
        <TabList
          tabList={tabList}
          handleTabClick={handleTabClick}
          activeTab={tabList.indexOf(activeTab)}
          disabledNav={disabledNav}
        />
      </AppBar>
      <div className="component-container">{Components[activeTab]}</div>
      <Dialog
        open={newVersionTitle}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title" style={{textAlign:"center"}} >
          {newVersionTitle}
        </DialogTitle>
        <DialogContent style={{textAlign:"left"}}>
          <DialogContentText id="alert-dialog-description">
            <b>A new software version is available.</b>

            {releaseNotes && (
              <div dangerouslySetInnerHTML={{
                __html: releaseNotes.replace(/(?:\r\n|\r|\n)/g, '<br>').replace(/(?:-)/g, '<b>&#9900;</b>')
              }} />
            )}

            <b>Would you like to update now?</b>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => {
              setFastTrackUpdate(false);
              setNewVersionTitle(false);
            }}
            color="primary" className={classes.okButton} autoFocus>
            Cancel
          </Button>
          <Button onClick={() => {
              setFastTrackUpdate(true);
              setActiveTab('Menu');
              setNewVersionTitle(false);
            }}
            color="primary" className={classes.okButton} autoFocus>
            Update
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default Home;
