import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Grid,
  Paper,
  Typography,
  Slider,
  Button,
  IconButton,
  FormGroup,
  FormControlLabel,
  FormControl,
  Switch
} from "@material-ui/core";
import {
  ArrowDownward,
  ArrowUpward,
  ExpandMore,
  RotateLeft,
  RotateRight,
  Settings,
  Videocam,
  ZoomIn,
  ZoomOut
} from "@material-ui/icons";
import React from "react";

import AppContext from "../../../../AppContext";

import "./style.css";

function SettingsPanel() {
  const [settingsOpen, setSettingsOpen] = React.useState(true);
  return (
    <>
      <div className="settingspanel">
          <AppContext.Consumer>
            {({
              showBounds,
              setShowBounds,
              showMagnet,
              setShowMagnet,
              showGrid,
              setShowGrid
            }) => (
              <Accordion expanded={settingsOpen} onChange={() => setSettingsOpen(!settingsOpen)}>
                <AccordionSummary
                  expandIcon={<ExpandMore />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                  <Typography>
                  Display Settings
                  </Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <FormControl component="fieldset">
                    <FormGroup>
                      <FormControlLabel
                        control={
                          <Switch
                            checked={showMagnet}
                            onChange={(e) => {
                              setShowMagnet(e.target.checked);
                            }}
                          />
                        }
                        label="Magnetic Print Bed"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            checked={showBounds}
                            onChange={(e) => {
                              setShowBounds(e.target.checked);
                            }}
                          />
                        }
                        label="Show Printer Bounds"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            checked={showGrid}
                            onChange={(e) => {
                              setShowGrid(e.target.checked);
                            }}
                          />
                        }
                        label="Show Grid"
                      />
                    </FormGroup>
                  </FormControl>
                </AccordionDetails>
              </Accordion>
            )}
          </AppContext.Consumer>
      </div>
    </>
  );
}

export default SettingsPanel;
