export const formatBytes = (bytes, decimals = 2) => {
  if (bytes === 0) return '0 Bytes';

  const k = 1024;
  const dm = decimals < 0 ? 0 : decimals;
  const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

  const i = Math.floor(Math.log(bytes) / Math.log(k));

  return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
};

export let storage = {};

export const fillStorage = () => {
  return getSettings().then((data) => {
    storage = data;
  });
};

export const setStorage = (key, val) => {
  storage[key] = val;
  let formData = new FormData();
  formData.append("json", JSON.stringify(storage));
  postSettings(formData);
};
export const getStorage = (key) => {
  return (typeof storage[key] !== 'undefined' ? storage[key] : false);
};
export const removeStorage = (key) => {
  storage[key] = null;
};
export const resetStorage = () => {
  return storageReset();
};
async function getSettings() {
    let response = null;
    response = await fetch(`http://${window.location.hostname}:2127/api/v1/get/ui/settings`, {
      method: "GET"
    });
    return await response.json(); // parses JSON response into native JavaScript objects
}

async function storageReset() {
  let settings = {};
  await getSettings().then((data) => {
    console.log('before', data);
    let setAll = (obj, val) => Object.keys(obj).forEach(
      k => obj[k] = (k == 'network' || k == 'printerName' ? obj[k] : val)
     );
    setAll(data, "");
    console.log('after', data);
    let formData = new FormData();
    formData.append("json", JSON.stringify(data));
    postSettings(formData);
    settings = data;
  });
  return settings;
}

async function postSettings(formData) {
  await fetch(`http://${window.location.hostname}:2127/api/v1/set/ui/settings`, {
    method: "POST",
    body: formData // send updated settings
  });
}
