import * as THREE from "three";
import { Interaction } from 'three.interaction';
import React, { Component } from "react";
import "./style.css";
import { OrbitControls } from '../RenderingUtils/OrbitControls';
import STLLoader from "../RenderingUtils/STLLoader";
import AppContext from "../../../../AppContext";
import { ScreenShare, StarTwoTone } from "@material-ui/icons";
import {
  Button,
  ButtonGroup,
  Grid,
  Paper,
  FormGroup,
  FormControlLabel,
  FormControl,
  FormLabel,
  RadioGroup,
  Radio
} from "@material-ui/core";
import {
  Loop,
  OpenWith,
  OpenInNew
} from "@material-ui/icons";
const TransformControls = require('../RenderingUtils/TransformControls')(THREE);

function deg2rad(deg) {
  return deg * (Math.PI / 180);
}

function rad2deg(rad) {
  return (Math.PI * rad) / 180;
}

class PrintRenderer extends Component {
  constructor(props) {
    super(props);

    this.canvas = React.createRef();
    this.canvasBg = null;
    this.rotationTarget = 0.0;
    this.lastTime = null;

    this.renderer = null;
    this.scene = null;
    this.control = null;
    this.cameraHelper = null;
    this.cameraPivot = null;
    this.camera = null;
    this.zTarget = 0;
    this.gridObj = null;
    this.magnetObj = null;
    this.planeObj = null;
    this.boundsObj = null;

    this.showMagnetBed = false;
    this.showBounds = false;
    this.showGrid = false;
    this.viewControls = null;
  }

  static contextType = AppContext;

  shouldComponentUpdate(nextProps, nextState) {
    console.log("test");
  }

  componentDidMount() {
    this.assignCanvas();
  }

  // GETING DATA FORM SELECT ELEMENT TO SET POSITION SCALE OR ROTATION IN TRANSFORMCONTROL
  posRotScale = (value) => {

      this.context.setTransformValue(value);

      switch (value) {

        case "position":
          this.control.enabled = true;
          this.control.setMode( "translate" );
          this.control.showX = true;
          this.control.showY = true;
          this.control.showZ = true;
          break;

        case "rotation":
          this.control.enabled = true;
          this.control.setMode( "rotate" );
          this.control.showX = true;
          this.control.showY = true;
          this.control.showZ = true;
          break;

        case "scale":
          this.control.enabled = true;
          this.control.setMode( "scale" );
          this.control.showX = true;
          this.control.showY = true;
          this.control.showZ = true;
          break;

        case "none":
          this.control.enabled = false;
          this.control.showX = false;
          this.control.showY = false;
          this.control.showZ = false;
          break;

        default:;

    }
  };

  assignCanvas() {
    // this.canvas = canvas;
    let canvasBg = this.canvas.current.parentElement;
    this.canvas.current.width = canvasBg.clientWidth;
    this.canvas.current.height = canvasBg.clientHeight;
    this.initCanvas(
      this.canvas.current,
      this.canvas.current.width,
      this.canvas.current.height
    );
  }

  initCanvas(canvas, width, height) {
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(75, width / height, 0.1, 1000);

    const renderer = new THREE.WebGLRenderer({ canvas });
    renderer.setSize(width, height);
    scene.background = new THREE.Color( 0xd1d1d1 );
    const interaction = new Interaction(renderer, scene, camera);
    //Create a ground plane to display our mesh on
    //const gpTexture = new THREE.TextureLoader().load("./buildplane.png");
    const gpMesh = new THREE.Mesh(
      new THREE.PlaneGeometry(800, 800, 1, 1),
      new THREE.MeshBasicMaterial({ color: 0xd1d1d1 })
    );
    scene.add(gpMesh);
    this.planeObj = gpMesh;

    //Create alternate ground plane that renders the magnetic build surface
    const magTexture = new THREE.TextureLoader().load("./magnet.png");
    const magMesh = new THREE.Mesh(
      new THREE.PlaneGeometry(341.3, 341.3, 1, 1),
      new THREE.MeshBasicMaterial({ map: magTexture, transparent:true })
    );
    magMesh.position.y = -10;
    magMesh.position.z = -1;
    scene.add(magMesh);
    this.magnetObj = magMesh;
    this.magnetObj.visible = false;

    const path = new THREE.Path();
    path.moveTo(-150, -150);
    path.lineTo(150, -150);
    path.lineTo(150, 150);
    path.lineTo(-150, 150);
    path.lineTo(-150, -150);
    const pathMesh = new THREE.Line(
      new THREE.BufferGeometry().setFromPoints(path.getPoints()),
      new THREE.LineBasicMaterial({ color: 0xff0000, linewidth: 1 })
    );
    pathMesh.position.z = 0.25;
    scene.add(pathMesh);
    pathMesh.visible = false;
    this.boundsObj = pathMesh;

    // Add grid visual
    let grid = new THREE.GridHelper(300, 30, 0x555555, 0x555555);
    grid.rotation.set(Math.PI / 2, 0, 0);
    grid.position.set(0, 0, 0);
    scene.add(grid);
    grid.visible = false;
    this.gridObj = grid;
    let zTarget = this.zTarget;
    let rotationTarget = this.rotationTarget;
    let currentContext = this.context;

    let buildGeometry = new THREE.BoxGeometry(300, 300, 400, 10, 10, 10);
    let buildEdges = new THREE.EdgesGeometry( buildGeometry );
    let buildLines = new THREE.LineSegments( buildEdges, new THREE.LineBasicMaterial( { color: 0x555555 } ) );
    buildLines.position.z = 200;
    scene.add(buildLines);

    camera.up = new THREE.Vector3( 0, 0, 1 );

    let control = new TransformControls( camera, renderer.domElement );
    this.viewControls = new OrbitControls( camera, renderer.domElement );
    let cameraControl = this.viewControls;

    camera.position.y = -400;
    camera.position.z = 200;

    cameraControl.update();
    // TODO: bring this back if we re-enable the NavigationPanel
    //this.viewControls.addEventListener( 'change', function (e) {
    //     currentContext.setZoom((camera.position.y + 350)  / 3);
    //     currentContext.addRotation(rad2deg(rotationTarget) - currentContext.camRotation);
    //     currentContext.addZ(zTarget);
    //});

    let mesh = null;
    let context = this.context;
    control.enabled = false;
    control.showX = false;
    control.showY = false;
    control.showZ = false;
    control.setSize(2);
    cameraControl.maxDistance = 900.00;

    const objRoot = new THREE.Group();
    const loader = new STLLoader();
    console.log("stl", this.context.stl);

    if(this.context.stl) {
      if(typeof this.context.stl == "string") {
        this.context.setFileName(this.context.stl);
        loader.load(`http://${window.location.hostname}:9990/${this.context.stl}`, (geo) => {
          const mat = new THREE.MeshPhongMaterial({
            color: 0xffffff,
            specular: 0x111111,
            shininess: 200
          });
          //   const mat = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
          mesh = new THREE.Mesh(geo, mat);
          mesh.name = this.context.stl;
          mesh.position.set(0, 0, 0);
          //   m.scale.set(0.05, 0.05, 0.05);
          //   m.setRotationFromEuler(new THREE.Euler(deg2rad(-90), 0, 0));
          mesh.geometry.center();
          control.attach( mesh );
          mesh.position.z = new THREE.Box3().setFromObject(mesh).getSize().z * 0.5;
          control.addEventListener( 'objectChange', function( e ) {
            mesh.position.z = new THREE.Box3().setFromObject(mesh).getSize().z * 0.5;
          });
          control.addEventListener('mouseDown', function () {
              cameraControl.enabled = false;
          });
          control.addEventListener('mouseUp', function () {
              cameraControl.enabled = true;
          });
          control.enabled = false;
          control.showX = false;
          control.showY = false;
          control.showZ = false;

          mesh.on('click', function(ev) {
            console.log("clicked "+mesh.name+"!");
            control.attach(mesh);
            control.enabled = true;
            context.setSelectedFile(mesh.name);
            control.showX = true;
            control.showY = true;
            control.showZ = true;
            if(control.mode == 'translate') {
              context.setTransformValue('position');
            } else if(control.mode == 'rotate') {
              context.setTransformValue('rotation');
            } else {
              console.log(control.mode);
              context.setTransformValue(control.mode);
            }
          });

          mesh.on('touchend', function(ev) {
            console.log("touched "+mesh.name+"!");
            control.attach(mesh);
            control.enabled = true;
            context.setSelectedFile(mesh.name);
            control.showX = true;
            control.showY = true;
            control.showZ = true;
            if(control.mode == 'translate') {
              context.setTransformValue('position');
            } else if(control.mode == 'rotate') {
              context.setTransformValue('rotation');
            } else {
              console.log(control.mode);
              context.setTransformValue(control.mode);
            }
          });

          objRoot.add(mesh);
          this.context.setModifiedStl(mesh);
          scene.add( control );
        });

      } else {
        let reader = new FileReader();
        let context = this.context;
        context.setFileName(this.context.stl.name);
        reader.addEventListener( 'load', function ( event ) {

          let contents = event.target.result;
          let geometry = new STLLoader().parse( contents );
          console.log(contents);
          geometry.sourceType = "stl";
          geometry.sourceFile = contents;
          let material = new THREE.MeshPhongMaterial( { color: 0xffffff, specular: 0x111111, shininess: 200 } );

          mesh = new THREE.Mesh( geometry, material );
          mesh.name = context.stl.name;
          mesh.position.set(
                0, //x
                1, //y
                0  //z
            );
            mesh.rotation.set( 0, 0, 0 );
            mesh.scale.set( 1, 1, 1 );
            mesh.castShadow = false;
            mesh.receiveShadow = false;
            mesh.geometry.center();
            control.attach( mesh );
            control.enabled = true;
            mesh.position.z = new THREE.Box3().setFromObject(mesh).getSize().z * 0.5;
            control.addEventListener( 'objectChange', function( e ) {
              mesh.position.z = new THREE.Box3().setFromObject(mesh).getSize().z * 0.5;
            });
            control.addEventListener('mouseDown', function () {
                cameraControl.enabled = false;
            });
            control.addEventListener('mouseUp', function () {
                cameraControl.enabled = true;
            });

            mesh.on('click', function(ev) {
              console.log("clicked "+mesh.name+"!");
              control.attach(mesh);
              control.enabled = true;
              context.setSelectedFile(mesh.name);
              control.showX = true;
              control.showY = true;
              control.showZ = true;
              if(control.mode == 'translate') {
                context.setTransformValue('position');
              } else if(control.mode == 'rotate') {
                context.setTransformValue('rotation');
              } else {
                console.log(control.mode);
                context.setTransformValue(control.mode);
              }
            });

            mesh.on('touchend', function(ev) {
              console.log("touched "+mesh.name+"!");
              control.attach(mesh);
              control.enabled = true;
              context.setSelectedFile(mesh.name);
              control.showX = true;
              control.showY = true;
              control.showZ = true;
              if(control.mode == 'translate') {
                context.setTransformValue('position');
              } else if(control.mode == 'rotate') {
                context.setTransformValue('rotation');
              } else {
                console.log(control.mode);
                context.setTransformValue(control.mode);
              }
            });

            objRoot.add(mesh);
            context.setModifiedStl(mesh);
            scene.add( control );
        }, false );

        reader.readAsBinaryString(this.context.stl);
      }
    } else {
      control.addEventListener('mouseDown', function () {
          cameraControl.enabled = false;
      });
      control.addEventListener('mouseUp', function () {
          cameraControl.enabled = true;
      });
    }

    scene.add(objRoot);
    //control.attach( objRoot );
    scene.add( control );

    scene.add(camera);
    scene.add(new THREE.HemisphereLight(0xffffff, 0x111533));

    scene.on('click', function(ev) {
      console.log(ev);
      let matchCount = 0;
      for(let idx in ev.intersects) {
        let intersectedPlane = ev.intersects[idx].object;
        if(objRoot.children.indexOf(intersectedPlane) !== -1) {
          matchCount++;
        }
      }
      if(matchCount == 0) {
        console.log("not a printable object!");
        control.detach();
        control.enabled = false;
        context.setSelectedFile(null);
      }
    });

    scene.on('touchend', function(ev) {
      console.log(ev);
      let matchCount = 0;
      for(let idx in ev.intersects) {
        let intersectedPlane = ev.intersects[idx].object;
        if(objRoot.children.indexOf(intersectedPlane) !== -1) {
          matchCount++;
        }
      }
      if(matchCount == 0) {
        console.log("not a printable object!");
        control.detach();
        control.enabled = false;
        context.setSelectedFile(null);
      }
    });

    this.camera = camera;
    this.renderer = renderer;
    this.scene = scene;
    this.control = control;
    //this.cameraHelper = cameraHelper;
    //this.cameraPivot = cameraPivot;

    this.context.setControl(this.control);
    this.context.setScene(this.scene);
    this.context.setCamera(this.camera);
    this.context.setObjRoot(objRoot);

    this.renderFrame();
  }

  renderFrame() {
    requestAnimationFrame(this.renderFrame.bind(this));
    // TODO bring this back if re-enabling NavigationPanel
    // let camCurrent = this.cameraPivot.rotation.z;
    // this.cameraPivot.rotation.z = camCurrent;
    // this.cameraPivot.rotation.z= THREE.MathUtils.lerp(
    //   camCurrent,
    //   this.rotationTarget,
    //   0.01
    // );
    //
    // let currentHeight = this.cameraPivot.position.z;
    // this.cameraPivot.position.z = THREE.MathUtils.lerp(
    //   currentHeight,
    //   this.zTarget,
    //   0.01
    // );
    this.viewControls.update();
    this.renderer.render(this.scene, this.camera);
  }

  render() {
    let value = this.context;

    try {
      // TODO bring this back if re-enabling NavigationPanel
      // this.camera.position.y = -350 + value.camZoom * 3;
      // this.rotationTarget = deg2rad(value.camRotation);
      // this.zTarget = value.camZ;
      this.viewControls.update();

      this.boundsObj.visible = value.showBounds;
      this.planeObj.visible = !value.showMagnet;
      this.magnetObj.visible = value.showMagnet;
      this.gridObj.visible = value.showGrid;
    } catch (err) {}

    console.log("test2");
    return (
      <>
        <div className="transform-panel">
          <ButtonGroup
            orientation="vertical"
            color="primary"
            aria-label="vertical outlined primary button group"
          >
            <Button onClick={() => this.posRotScale("scale")}
                    style={{
                      backgroundColor: this.context.transformValue == 'scale' ? '#0071ce' : '#fff',
                      color: this.context.transformValue == 'scale' ? '#fff' : '#5f6368',
                      width: 120,
                      height: 45
                    }}
            >
              <OpenInNew style={{left: -10, position: 'relative'}} /> Scale
            </Button>
            <Button onClick={() => this.posRotScale("position")}
                    style={{
                      backgroundColor: this.context.transformValue == 'position' ? '#0071ce' : '#fff',
                      color: this.context.transformValue == 'position' ? '#fff' : '#5f6368',
                      width: 120,
                      height: 45
                    }}
            >
              <OpenWith style={{left: -12, position: 'relative'}} /> Move
            </Button>
            <Button onClick={() => this.posRotScale("rotation")}
                    style={{
                      backgroundColor: this.context.transformValue == 'rotation' ? '#0071ce' : '#fff',
                      color: this.context.transformValue == 'rotation' ? '#fff' : '#5f6368',
                      width: 120,
                      height: 45
                    }}
            >
              <Loop style={{left: -6, position: 'relative'}} /> Rotate
            </Button>
          </ButtonGroup>
        </div>
        <div className="render-canvas-bg">
          <canvas id="three-scene" className="render-canvas" ref={this.canvas}></canvas>
        </div>
      </>
    );
  }
}

export default PrintRenderer;
