import React, { useState, useMemo, useRef } from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import Setup from "./components/Setup/Setup";
import Home from "./components/Home/Home";
import {
  createMuiTheme,
  MuiThemeProvider
  // makeStyles,
} from "@material-ui/core/styles";
import {
  AppBar,
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Snackbar,
  Typography
} from "@material-ui/core";
import MuiAlert from '@material-ui/lab/Alert';
import { getStorage, setStorage, fillStorage } from "./Utility/helpers";
import { useMountEffect } from "./Utility/useMountEffect";
import useWebSocket, { ReadyState } from "react-use-websocket";
import TempContext from "./TempContext";
import imgPrint from "./img/mm-print.svg";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#0071CE"
    },
    secondary: {
      main: "#CD6930"
    },
    background: {
      default: "#3A3A3A"
    },
    type: "dark"
  },
  overrides: {
    MuiTab: {
      "root": {
        "&$selected": {
          "color": "#FFFFFF",
        },
        "font-weight": 300,
      }
    },
    MuiAppBar: {
      "root": {
        "padding": "10px"
      }
    },
    MuiPaper: {
      rounded: {
        "border-radius": "15px",
      }
    },
    MuiButton: {
      "root": {
        "font-weight": 300
      }
    },
    MuiButtonBase: {
      "root": {
        "font-weight": 300
      }
    },
    MuiIconButton: {
      "root": {
        "font-weight": 300
      }
    },
    MuiListItemText: {
      "root": {
        "font-weight": 300
      }
    },
    MuiAccordion: {
      "root": {
        "&$expanded": {
          margin: 0
        }
      }
    },
    MuiAccordionSummary: {
      "root": {
        maxHeight: 40,
        minHeight: 10,
        "&$expanded": {
          maxHeight: 40,
          minHeight: 10
        }
      }
    },
    MuiAccordionDetails: {
      "root": {
        "background-color": "#d1d1d1",
        "padding": "8px",
        "color": "#000",
        "margin": 5,
        "border-radius": 1
      }
    },
    MuiDialogActions: {
      "root": {
        "justify-content": "space-around"
      }
    },
    MuiTypography: {
      "root": {
        "font-weight": 300
      },
      h1: {
        "font-weight": 300
      },
      h2: {
        "font-weight": 300
      },
      h3: {
        "font-weight": 300
      },
      h4: {
        "font-weight": 300
      },
      h5: {
        "font-weight": 300
      },
      h6: {
        "font-weight": 300
      },
      body1: {
        "font-weight": 300
      },
      body2: {
        "font-weight": 300
      }
    }
  }
});
// const useStyles = makeStyles({});
const App = () => {
  // const classes = useStyles();

  const [mode, setActiveMode] = useState(null);
  const [onboarded, setOnboarded] = useState(true);
  const [loading, setLoading] = useState(true);
  const [bedTemp, setBedTemp] = useState(0);
  const [bedTargetTemp, setBedTargetTemp] = useState(0);
  const [nozzleTemp, setNozzleTemp] = useState(0);
  const [nozzleTargetTemp, setNozzleTargetTemp] = useState(0);
  const [zOffset, setZOffset] = useState(0);
  const [machineName, setMachineName] = useState("");
  const [userLevelDialog, setUserLevelDialog] = useState(false);
  const [errorDialog, setErrorDialog] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [errorTitle, setErrorTitle] = useState("");
  const [showSnack, setShowSnack] = useState(false);
  const [snackText, setSnackText] = useState("");

  const handleClose = (event, reason) => {
    setShowSnack(false);
  };

  useMountEffect(() => {
   setLoading(true);
   fillStorage().then(function() {
     setOnboarded(getStorage("onboarded"));

     if(!getStorage("userMode") || getStorage("userMode") == "standard") {
       setStorage("userMode", null);
       setUserLevelDialog(true);
     } else {
       setActiveMode(getStorage("userMode"));
     }

     let networkType = getStorage("networkProfile");
     let printerName = getStorage("printerName");

     if (networkType === "hotspot") {
       fetch(`http://${window.location.hostname}:8990/hotspot/connect`, {
         method: "POST",
         headers: {
           "Content-Type": "application/json"
         }
       });
     }

     if (networkType === "wifi") {
       fetch(`http://${window.location.hostname}:8990/hotspot/disconnect`, {
         method: "POST",
         headers: {
           "Content-Type": "application/json"
         }
       });
     }

     if (printerName === null) {
       setStorage("printerName", "300x");
       setMachineName("300x");
     } else {
       if(printerName === "300X") {
         setStorage("printerName", "300x");
         setMachineName("300x");
       } else {
         setMachineName(printerName);
       }
     }
   });
  });

  const handleOnboarding = (val) => {
    setActiveMode(val);
    setOnboarded(true);
  };
  const [socketUrl, setSocketUrl] = useState(
    `ws://${window.location.hostname}:9000/sockjs/718/qerksnvm/websocket`
  );
  const messageHistory = useRef([]);

  const { sendMessage, lastMessage, readyState } = useWebSocket(socketUrl, {
    //Will attempt to reconnect on all close events, such as server shutting down
    shouldReconnect: (closeEvent) => true,
    reconnectAttempts: 999999, // default: 20
    reconnectInterval: 0, // default: 5000
    onOpen: () => {
      console.log("socket opened");
    },
    onError: () => {
      console.log("socket error");
    },
    onClose: () => {
      // force reconnect somehow like sending message?
      console.log("socket closed");
    },
    onMessage: (msg) => {
      // console.log("MESSAGE EVENT");
      try {
        let json = JSON.parse(msg.data.substring(1));
        if (json.length && json.length > 0) {
          if (json[0].hasOwnProperty("current") && json[0].current.temps[0]) {
            if(json[0].current.temps[0].hasOwnProperty("tool0") &&
               json[0].current.temps[0].hasOwnProperty("bed")
            ) {
              let temps = json[0].current.temps[0];
              console.log("setting temps");
              setNozzleTemp(parseInt(temps.tool0.actual));
              setBedTemp(parseInt(temps.bed.actual));
              setNozzleTargetTemp(parseInt(temps.tool0.target));
              setBedTargetTemp(parseInt(temps.bed.target));
            }
          }

          if (json[0].hasOwnProperty("event") && json[0].event.type == "Error") {
            // set error
            let error = json[0].event;
            console.log(error);
            let title = "Error thrown by " + error.payload.reason;
            let message = error.payload.error;
            setErrorMessage(message);
            setErrorTitle(title);
            setErrorDialog(true);
          }

          if (errorDialog && json[0].hasOwnProperty("current") && json[0].current.state) {
            if(json[0].current.state.text == "Operational") {
              setErrorDialog(false);
              setSnackText("Your Printer Is Back Online!");
              setShowSnack(true);
            }
          }

        }
      } catch (error) {}
    // console.log(msg);
    }
  });

  messageHistory.current = useMemo(() => {
    try {
      messageHistory.current.concat(lastMessage);
      // let json = JSON.parse(lastMessage.data.substring(1));
      // if (json[0].hasOwnProperty("current") && json[0].current.temps[0]) {
      //   if (json[0].current.temps[0]) {
      //     let temps = json[0].current.temps[0];
      //     setNozzleTemp(parseInt(temps.tool0.actual));
      //     setBedTemp(parseInt(temps.bed.actual));
      //   }
      // }
    } catch (error) {}
  }, [lastMessage]);

  const connectionStatus = {
    [ReadyState.CONNECTING]: "Connecting",
    [ReadyState.OPEN]: "Open",
    [ReadyState.CLOSING]: "Closing",
    [ReadyState.CLOSED]: "Closed",
    [ReadyState.UNINSTANTIATED]: "Uninstantiated"
  }[readyState];

  React.useEffect(() => {
    console.log(connectionStatus);
    if (connectionStatus == "Open") {
      sendMessage('{ "auth": "E7BFE47F2DBF489B9E78A3D9D042874A" }');
      sendMessage('{ "throttle": 2 }');
      sendMessage('{ "current": "state" }');
      setLoading(false);
    }
  }, [connectionStatus]);

  return (

    <MuiThemeProvider theme={theme}>
      <TempContext.Provider
        value={{
          bedTarget: bedTargetTemp,
          bed: bedTemp,
          nozzleTarget: nozzleTargetTemp,
          nozzle: nozzleTemp,
          zOffset: zOffset,
          setBedTarget: setBedTargetTemp,
          setNozzleTarget: setNozzleTargetTemp,
          setBed: setBedTemp,
          setNozzle: setNozzleTemp,
          setZOffset: setZOffset
        }}
      >
        <CssBaseline />
        <Box height="100vh" display="flex" flexDirection="column">
          {onboarded && (
            <AppBar position="sticky">
              <Typography variant="h6" style={{ textAlign: "center" }}>
                Printer Name: {window.location.hostname}
              </Typography>
            </AppBar>
          )}
          {loading ? (
            <>
              <img
                src={imgPrint}
                style={{
                  width: 200,
                  height: 200,
                  zIndex: 99999999,
                  position: "absolute",
                  left: "calc(50% - 100px)",
                  top: "calc(50% - 100px)"
                }}
              ></img>
              <h4
                style={{
                  width: 200,
                  zIndex: 99999999,
                  position: "absolute",
                  left: "calc(50% - 100px)",
                  top: "calc(50% + 120px)",
                  textAlign: "center"
                }}
              >
                Loading ...
              </h4>
            </>
          ) : (
            <Home
              mode={mode}
              lastMessage={lastMessage}
              setActiveMode={setActiveMode}
            />
          )}
          <Dialog
            open={errorDialog}
            disableBackdropClick={true}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            style={{textAlign:"center"}}
          >
            <DialogTitle id="alert-dialog-title">{errorTitle}</DialogTitle>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                {errorMessage}
                <br />
                Turn off your printer and check to make sure you have no loose wire connections.
                Once you have checked all connections, turn your printer back on and try again.
              </DialogContentText>
            </DialogContent>
          </Dialog>
          <Dialog
            open={userLevelDialog}
            disableBackdropClick={true}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            style={{textAlign:"center"}}
          >
            <DialogTitle id="alert-dialog-title">New User Levels Unlocked</DialogTitle>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                New User Levels Unlocked. Go into settings to change your user level.
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => {
                  setStorage("userMode", "advanced");
                  setActiveMode("advanced");
                  setUserLevelDialog(false);
                }}
                color="primary" style={{
                  backgroundColor: "#0072CE",
                  color: "white",
                  marginRight: 10,
                  marginBottom: 10
                }} autoFocus>
                Set Advanced User Level
              </Button>
            </DialogActions>
          </Dialog>
          <Snackbar
            open={showSnack}
            autoHideDuration={6000}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'center',
            }}
            onClose={handleClose}
            style={{top:'45%'}}
          >
            <Alert onClose={handleClose} autoHideDuration={6000} severity="success">
              {snackText}
            </Alert>
          </Snackbar>
        </Box>
      </TempContext.Provider>
    </MuiThemeProvider>
  );
};

export default App;
