                  camRotation: this.state.camRotation,
                  camZoom: this.state.camZoom,
                  camZ: this.state.camZ,
                  camera: this.state.camera,
                  showMagnet: this.state.showMagnet,
                  showBounds: this.state.showBounds,
                  showGrid: this.state.showGrid,
                  stl: this.props.file,
                  transformValue: this.state.transformValue,
                  modifiedStl: this.state.modifiedStl,
                  fileName: this.state.fileName,
                  control: this.state.control,
                  scene: this.state.scene,
                  objRoot: this.state.objRoot,
                  selectedFile: this.state.selectedFile,
                  setModifiedStl: (stl) => {
                    this.setState({ modifiedStl: stl });
                  },
                  setFileName: (name) => {
                    this.setState({ fileName: name });
                  },
                  addRotation: (deg) => {
                    addRotation(deg);
                  },
                  setZoom: (zoom) => {
                    setZoom(zoom);
                  },
                  addZ: (z) => {
                    addZ(z);
                  },
                  setTransformValue: (value) => {
                    this.setState({ transformValue: value });
                  },
                  setCamera: (val) => {
                    this.setState({ camera: val });
                  },
                  setShowMagnet: (val) => {
                    this.setState({ showMagnet: val });
                  },
                  setShowBounds: (val) => {
                    this.setState({ showBounds: val });
                  },
                  setShowGrid: (val) => {
                    this.setState({ showGrid: val });
                  },
                  setControl: (control) => {
                    this.setState({ control: control });
                  },
                  setScene: (scene) => {
                    this.setState({ scene: scene });
                  },
                  setObjRoot: (objRoot) => {
                    this.setState({ objRoot: objRoot });
                  },
                  setSelectedFile: (file) => {
                    this.setState({ selectedFile: file });
                  },
