import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Checkbox,
  Divider,
  Grid,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  ListItemSecondaryAction,
  Paper,
  Typography,
  Slider,
  Button,
  IconButton,
  FormGroup,
  FormControlLabel,
  FormControl
} from "@material-ui/core";
import {
  Add,
  DeleteForever,
  Description,
  ExpandMore
} from "@material-ui/icons";
import React, { useContext, useState } from 'react';
import AppContext from "../../../../AppContext";
import * as THREE from "three";
import STLLoader from "../RenderingUtils/STLLoader";
import { useMountEffect } from "../../../../Utility/useMountEffect";

import "./style.css";

async function getFile(fileName) {
  // await response of fetch call
  let response = await fetch(`http://${window.location.hostname}:9990/${fileName}`);
  // only proceed once promise is resolved
  let data = await response.blob();
  // only proceed once second promise is resolved
  return data;
}

function FilesPanel() {
  const context = useContext(AppContext)

  let fileName = null;

  if(context.stl) {
    if(typeof context.stl == "string") {
      fileName = context.stl;
    } else {
      fileName = context.stl.name;
    }
  }

  const [listOpen, setListOpen] = React.useState(true);
  const [fileList, setFileList] = React.useState([]);
  const [uploadElement, setUploadElement] = useState(null);
  const handleListItemClick = (event, currentFile) => {
    context.setSelectedFile(currentFile.name);
  };
  const swapTrasformTool = (e, file) => {
    for (let idx in context.objRoot.children) {
      let mesh = context.objRoot.children[idx];
      if(mesh.name === file.name) {
        context.control.attach(mesh);
      }
    }
  };

  const deleteFile = (file) => {
    console.log('deleting '+file.name)
    for (let idx in context.objRoot.children) {
      let mesh = context.objRoot.children[idx];
      if(mesh.name === file.name) {
        context.objRoot.remove(mesh);
      }
    }

    let filesWithoutDeleted = fileList.filter(function( obj ) {
    return obj.name !== file.name;
    });

    if(filesWithoutDeleted) {
      setFileList(filesWithoutDeleted);
    }
  };

  useMountEffect(() => {
    if(context.stl) {
      if(typeof context.stl == "string") {
        getFile(fileName).then(function(data) {
          let file = new File([data], fileName);
          setFileList([{
              'name': fileName,
              'file': file
            }]
          );
        });
      } else {
        setFileList([{
          'name': fileName,
          'file': context.stl
        }]);
      }
    }

  });

  return (
    <>
      <div className="files-panel">
          <AppContext.Consumer>
            {() => (
              <>
              <Accordion expanded={listOpen} onChange={() => setListOpen(!listOpen)}>
                <AccordionSummary
                  expandIcon={<ExpandMore />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                  <Typography>
                  Object List
                  </Typography>
                </AccordionSummary>
                <AccordionDetails style={{justifyContent:"center"}}>
                <Grid container direction="column" item xs>
                  {fileList.length > 0 && (
                    <Grid item xs>
                    <List aria-label="file-list" style={{width:'100%'}}>
                    {fileList.length > 0 && fileList.map((currentFile, index) => {
                      return (
                        <ListItem
                          button
                          selected={context.selectedFile === currentFile.name}
                          onClick={(event) => handleListItemClick(event, currentFile)}
                          style={{backgroundColor:'#b7b7b7'}}
                          key={index}
                        >
                          <ListItemIcon>
                            <Checkbox
                              checked={context.selectedFile === currentFile.name}
                              onChange={ (e) => swapTrasformTool(e, currentFile)}
                              color="primary"
                              inputProps={{ 'aria-label': 'secondary checkbox' }}
                            />
                          </ListItemIcon>
                          <ListItemText primary={currentFile.name} />
                          <ListItemSecondaryAction>
                            <IconButton edge="end" aria-label="delete-file" onClick={(event) => deleteFile(currentFile)}>
                              <DeleteForever style={{color:'#ca0000'}}/>
                            </IconButton>
                          </ListItemSecondaryAction>
                        </ListItem>
                      )
                    })}
                    </List>
                    </Grid>
                  )}
                  <Grid container item direction="column" alignItems="center" justify="center" xs>
                  <Grid item>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() => {
                      uploadElement.click();
                    }}
                  >
                    <Add />
                     Add New STL
                     <input
                       style={{ display: "none" }}
                       type="file"
                       onChange={async (e) => {
                         let file = e.target.files[0];
                         if(file) {
                           setFileList([...fileList, {
                             'name': file.name,
                             'file': file
                           }]);
                           let match = /.+\.(stl)$/gi.exec(file.name);
                           if (match) {
                             const fileType = match[1].toLowerCase();
                             if (fileType === "stl") {
                               let reader = new FileReader();
                               reader.addEventListener( 'load', function ( event ) {

                                 let contents = event.target.result;
                                 console.log(contents);
                                 let geometry = new STLLoader().parse( contents );
                                 console.log(contents);
                                 geometry.sourceType = "stl";
                                 geometry.sourceFile = contents;
                                 let material = new THREE.MeshPhongMaterial( { color: 0xffffff, specular: 0x111111, shininess: 200 } );

                                 let mesh = new THREE.Mesh( geometry, material );
                                 mesh.name = contents.name;
                                 mesh.position.set(
                                       0, //x
                                       1, //y
                                       0  //z
                                   );
                                   mesh.rotation.set( 0, 0, 0 );
                                   mesh.scale.set( 1, 1, 1 );
                                   mesh.name = file.name;
                                   mesh.castShadow = false;
                                   mesh.receiveShadow = false;
                                   mesh.geometry.center();
                                   mesh.position.z = new THREE.Box3().setFromObject(mesh).getSize().z * 0.5;
                                   context.control.addEventListener( 'objectChange', function( e ) {
                                     mesh.position.z = new THREE.Box3().setFromObject(mesh).getSize().z * 0.5;
                                   });

                                   mesh.on('click', function(ev) {
                                     console.log("clicked "+mesh.name+"!");
                                     context.control.attach(mesh);
                                     context.control.enabled = true;
                                     context.setSelectedFile(mesh.name);
                                     context.control.showX = true;
                                     context.control.showY = true;
                                     context.control.showZ = true;
                                     if(context.control.mode == 'translate') {
                                       context.setTransformValue('position');
                                     } else if(context.control.mode == 'rotate') {
                                       context.setTransformValue('rotation');
                                     } else {
                                       console.log(context.control.mode);
                                       context.setTransformValue(context.control.mode);
                                     }
                                   });

                                   mesh.on('touchend', function(ev) {
                                     console.log("touched "+mesh.name+"!");
                                     context.control.attach(mesh);
                                     context.control.enabled = true;
                                     context.setSelectedFile(mesh.name);
                                     context.control.showX = true;
                                     context.control.showY = true;
                                     context.control.showZ = true;
                                     if(context.control.mode == 'translate') {
                                       context.setTransformValue('position');
                                     } else if(context.control.mode == 'rotate') {
                                       context.setTransformValue('rotation');
                                     } else {
                                       console.log(context.control.mode);
                                       context.setTransformValue(context.control.mode);
                                     }
                                   });

                                   context.objRoot.add(mesh);
                                   //combine all meshes into one object group
                                   context.setModifiedStl(context.objRoot);
                                   //scene.add( control );
                               }, false );
                               reader.readAsBinaryString(file);
                             }
                           } else {
                             //Something wrong with file
                           }

                         }
                       }}
                       ref={(ref) => {
                         setUploadElement(ref);
                       }}
                       accept=".stl,.STL"
                     />
                  </Button>
                  </Grid>
                  </Grid>
                  </Grid>
                </AccordionDetails>
              </Accordion>
              </>
            )}
          </AppContext.Consumer>
      </div>
    </>
  );
}

export default FilesPanel;
